import React, { ChangeEvent, FormEvent, useEffect, useState } from 'react';
import { getDownloadURL, getStorage, ref, uploadBytes, uploadBytesResumable, uploadString } from 'firebase/storage';
import { app, storage } from './firebase'
import styled from 'styled-components'
import { GlobalStyle } from './components/Globalstyle'

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-direction: column;
`

function App() {
  const [ file, setFile ] = useState<any>([]);
  const [ preview, setPreview ] = useState<any>([]);
  const [ loading, setLoading ] = useState(false);
  const [ download, setDownload ] = useState<any>([])

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    e.preventDefault();
    if (e.target.files?.length === 0) return;

    const files = e.target.files!
    for (let i = 0; i < files?.length; i++) {
      const fR = new FileReader()
      fR.readAsDataURL(files[i]);
      fR.onload = () => {
        setPreview((prev: any) => {
          return [...prev, fR.result]
        })
      }
      setFile((prev: any) => [...prev, files[i]])
    }
  }

  const onSubmit = async(e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    const storageRef = ref(storage, 'images');

    const uploadTask = uploadBytesResumable(storageRef, file);

    uploadTask.on('state_changed', (snapshot) => {
      console.log(snapshot.bytesTransferred, snapshot.totalBytes)
      const progress: number = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
      console.log('Upload is ' + progress + '% done');

      switch (snapshot.state) {
        case 'paused':
          console.log('Upload is paused');
          break;
        case 'running':
          console.log('Upload is running');
          break;
      } 
    }, e => {
      console.log(e)
    }, () => {
      getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
        setDownload((prev: any) => [...prev,downloadURL])
        console.log('File available at');
      });
    })

  }
  
  useEffect(() => {
  }, [])


  return (
    <Container>
      <GlobalStyle />
      <form onSubmit={e => onSubmit(e)} >
        <input type={'file'}  accept='image/*' onChange={e => onChange(e)} />
        <button type='submit' disabled={file && !loading ? false : true} >제출</button>
      </form>

        미리보기
      <div>
        {preview.map((img: any, index: number) => (
        <div key={index} style={{width: '500px'}} >
          <img style={{objectFit: 'contain'}} width="500" sizes="(min-width: 600px) 200px, 50vw" src={img} />
        </div>))}
      </div>
    결과
      <div>
      {download}
      </div>
    </Container>
  );
}

export default App;
